#photos{
	width:210px;		    /*宽度*/
	height:280px;		   /*高度*/
	border:1px solid #ccc;  /*加一个灰色边框*/
	margin:260px auto;	   /*水平居中，高度距离顶部100px*/
    transform:rotateY(0deg);
    transform-style:preserve-3d;/*设置3d环境*/
    perspective:800px;		/*设置景深为800px*/
}
#photos img{
	width:100%;
	height:100%;
