<script>
export default {
    name: 'hello',
    data() {
        return {
        };
    },
    mounted() {
        this.onLoad();
    },
    methods: {
        onLoad() {
            let ocontent = document.getElementById("desc");
            let str="这是一份诗雅送给姗姗的毕业礼物，界面很丑，但我很用心，希望你会喜欢";
            let i = 0;
            let flag = null;
            function done() {
                if(i<str.length) {
                    ocontent.innerHTML=str.substring(0,i+1);
                    i=i+1;
                } 
                else {
                    clearInterval(flag);
                } 
                
            }
            flag = setInterval(done,100);

        }
    }
}
