import Vue from 'vue'
import VueRouter from 'vue-router' // 引入路由

// 引入文件
import HelloWorld from '../components/HelloWorld'
import Test from '../components/Test'
import Photo from '../components/Photo'
import RotatePhotos from '../components/RotatePhotos'

Vue.use(VueRouter)

export default new VueRouter({

  // 路由配置
  routes: [
    { path: '/', redirect: '/helloworld' },
    {
      // 路由的路径
      path: '/helloWorld',
      // 路由名称（可以不添加）
      name: 'helloWorld',
      // 跳转的组件
      component: HelloWorld
    },
    {
      path: '/test',
      // 路由名称（可以不添加）
      name: 'test',
      component: Test
    },
    {
      path: '/photo',
      // 路由名称（可以不添加）
      name: 'photo',
      component: Photo
    },
    {
      path: '/rotatePhotos',
      // 路由名称（可以不添加）
      name: 'rotatePhotos',
      component: RotatePhotos
    }
  ]
})
