                img3: require("../assets/shanshan24.jpeg"),
                title: "都是你",
                isOpen: false
            }
        ]
    };
  },
  mounted() {
      
    this.onLoad();
  },
  methods: {
    onLoad() {
        let ocontent = document.getElementById("show");
        let str="相识了10年，陪伴了我3年，遇到你，我很幸运，同时也感谢你包容我的小脾气，我想像中你毕业的时候我会去陪着你，一束玫瑰花，一个拥抱，但这些都没了，疫情这个第三者把我们都分开，想想就生气！！！看到你的照片，知道你过着很开心的大学生活，同时因为我少了这段经历，担心自己会跟不上你们的节奏、害怕我们的友谊会越来越疏远，你和雅思都是我的好闺蜜，好想以后就算不见面，我们也能像现在这样陪着对方度过每一个10年。。这一个小小的礼物就当是你毕业的礼物，希望你能喜欢它。。                              来自你的好朋友：雅";
        let i = 0;
        let flag = null;
        function done() {
            if(i<str.length) {
                ocontent.innerHTML=str.substring(0,i+1);
                i=i+1;
            } 
            else {
                clearInterval(flag);
            } 
            
        }
        flag = setInterval(done,100);
    },
    topOpen() {
      this.isOpenedTop = !this.isOpenedTop;
    },

    open(idx, isOpen) {
      if (this.isOpenedTop) {
        this.items[idx].isOpen = !isOpen;
      }
    },

    reset() {
      this.items.forEach(item => (item.isOpen = false));
      this.isOpenedTop = false;
    }
  }
}
